import React from 'react';
import { HashRouter, Route } from 'react-router-dom';

import './App.css';
import NavBar from './components/NavBar';
import Home from './components/Home';
import About from './components/About';
import Dev404 from './components/Dev404';

function App() {
  const navBarItems = [
    {num: 1, title: "Home", link: "/"},
    {num: 2, title: "About", link: "/about"},
    {num: 3, title: "Projects", link: "/projects"},
    {num: 4, title: "Contact", link: "/contact"},
    {num: 5, title: "Blog", link: "/blog"}
  ];

  return (
    <HashRouter basename="/">
      <div className="App">
        <NavBar items={ navBarItems } />
        <header className="App-header">
          <Route path="/" exact component={Home} />
          <Route path="/about" component={About} />
          <Route path="/projects" component={Dev404} />
          <Route path="/blog" component={Dev404} />
          <Route path="/contact" component={Dev404} />
        </header>
      </div>
    </HashRouter>
    
  );
}

export default App;
